"use strict";
const container = document.querySelector("#root");
const ajax = new XMLHttpRequest();
const content = document.createElement("div");
const NEWS_URL = "https://api.hnpwa.com/v0/news/1.json";
const CONTENT_URL = "https://api.hnpwa.com/v0/item/@id.json";
const store = {
  currentPage: 1,
};

function getData(url) {
  // ÎèôÍ∏∞Ï†ÅÏúºÎ°ú Ï≤òÎ¶¨Ìï† Í≤ΩÏö∞ ÎßàÏßÄÎßâ ÏòµÏÖò Í∞íÏùÑ falseÎ•º Ï£ºÎ©¥ Îê©ÎãàÎã§.
  ajax.open("GET", url, false);
  ajax.send();

  // JSONÌååÏùºÏùÑ Í∞ùÏ≤¥Ìôî ÏãúÏºúÏ£ºÍ∏∞ ÏúÑÌï¥ JSON.parseÎ•º ÏÇ¨Ïö©!
  return JSON.parse(ajax.response);
}

function newsFeed() {
  const newsFeed = getData(NEWS_URL);
  const newsFeedLength = newsFeed.length / 10;
  console.log(newsFeedLength);
  const newsList = [];

  newsList.push("<ul>");

  for (let i = (store.currentPage - 1) * 10; i < store.currentPage * 10; i++) {
    newsList.push(`
    <li>
      <a href="#/show/${newsFeed[i].id}">${newsFeed[i].title} (${newsFeed[i].comments_count})</a>
    </li>
    `);
  }
  newsList.push("</ul>");
  newsList.push(`
    <div>
      <a href='#/page/${store.currentPage > 1 ? store.currentPage - 1 : 1}'>Ïù¥Ï†Ñ ÌéòÏù¥ÏßÄ</a>
      <a href='#/page/${store.currentPage < newsFeedLength ? store.currentPage + 1 : 3}'>Îã§Ïùå ÌéòÏù¥ÏßÄ</a>
    </div>
  `);
  container.innerHTML = newsList.join("");
}

function newsDetail() {
  // substr(startIndex, length), substring(startIndex, endIndex), slice(startIndex, endIndex) Î¨∏ÏûêÏó¥ ÏûòÎùºÎÇ¥Îäî Î∞©Î≤ï!
  const id = location.hash.substr(7);
  const newsContent = getData(CONTENT_URL.replace("@id", id));

  container.innerHTML = `
    <h1>${newsContent.title}</h1>
    <div>
      <a href="#/page/${store.currentPage}">Î™©Î°ùÏúºÎ°ú</a>
    </div>
  `;
}

function router() {
  const routePath = location.hash;

  if (routePath === "") {
    newsFeed();
  } else if (routePath.indexOf("#/page/") >= 0) {
    store.currentPage = Number(routePath.substr(7));
    newsFeed();
  } else {
    newsDetail();
  }
}

window.addEventListener("hashchange", router);
router();
